.nav-item {
  min-width: 50px;
  margin: 0px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  background-color: lightgrey;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out
}

.nav-item:hover {
  cursor: pointer;
  background-color: white;
}

