body {
  background-color: #e8e8e8;
  font-family: 'Roboto', sans-serif;
}

.container {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.box {
  height: 80vh;
  width: 30vw;
  margin: .5%;
  background-color: #ffffff;
  opacity: 0;
  margin-top: 5%;
}

.left {
  -webkit-animation: .66s fadein .5s forwards;
          animation: .66s fadein .5s forwards;
  overflow-x: hidden;
}
.right {
  -webkit-animation: .66s fadein 1s forwards;
          animation: .66s fadein 1s forwards;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* overflow-y: scroll; */
}

@-webkit-keyframes fadein {
  100% {
    opacity: 1;
    margin-top: 0;
  }
}

@keyframes fadein {
  100% {
    opacity: 1;
    margin-top: 0;
  }
}

@media screen and (max-width: 680px) {
  .container {
    padding-top: 10%;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    width: 100%
  }
  .box {
    margin-bottom: 10px;
    height: 100%;
    width: 100%;
  }
}

@media screen and (max-width: 1080px) {
  .box {
    height: 500px;
    width: 300px;
  }
}



